import React,{ useState, useEffect } from 'react';

import Markdown from 'markdown-to-jsx';
import { Redirect } from 'react-router';
//includes
import '../../assets/css/default.min.css';
import '../../assets/css/about.min.css';
//Components
import HeaderLight from '../../components/headerComponent/HeaderLight';
import FooterLight from '../../components/footerComponent/footerLight';
import postlist from "../../posts.json"


function Post(props) {
    const validId = parseInt(props.match.params.id)
    if (!validId) {
        return <Redirect to="/404" />
    }
    const fetchedPost = {}
    let postExists = false
    postlist.forEach((post, i) => {
        if (validId === post.id) {
            fetchedPost.title = post.title ? post.title : "No title given"
            fetchedPost.date = post.date ? post.date : "No date given"
            fetchedPost.author = post.author ? post.author : "No author given"
            fetchedPost.content = post.content ? post.content : "No content given"
            postExists = true
        }
    })
    if (postExists === false) {
        return <Redirect to="/404" />
    }
    return (
        <div className="aboutPage light">
            <HeaderLight/> 
           <div className="container">
        
                <div className="row">
                    <div className="col-sm-2" style={{backgroundColor:"transparent"}}></div>
                    <div className="col-sm-8"style={{backgroundColor:"white"}}>

                            <h4 className='text-muted'>Published on {fetchedPost.date} by {fetchedPost.author} </h4>
                            <a href="#physics"> <h1 className="display-3">
                            {fetchedPost.title}</h1></a>
                            
                            <p className="h6">
                               
                               
                                <br></br>
                               
                             <Markdown options={{ wrapper: 'article' }}>{fetchedPost.content}</Markdown>
                          
    
                            <hr></hr>
            
                            </p>

                    </div>
                    <div className="col-sm-2" style={{backgroundColor:"transparent"}}></div>
                </div>

           </div>
            <FooterLight/>
        </div>
    )
}

export default Post
